import React from 'react';
import styles from '../../styles/AboutMe.module.css';
import portrait from '../../assets/Kevin.webp';

function AboutMe() {
  // Add image
  return (
    <div className={styles.AboutMe}>
      <picture>
        <img src={portrait} className={styles.myImage} alt="Kevin Kuang" />
      </picture>
      <section id="about-me">
        <h2>About Me</h2>
        <p>
          Hi, I'm Kevin. Ever since I was a kid, I've been fascinated by technology, 
          which is why I pursued a major in <strong>Math-CS</strong>. Whether its tinkering with computers 
          or developing software, Im driven by a desire to create user-friendly applications 
          that make a difference in peoples lives. Studying at the <strong>University of California, 
          San Diego (UCSD)</strong> has been an incredible experience. Its not just the rigorous coursework 
          thats been transformative, but also the hands-on projects and learning from some truly 
          inspiring professors. UCSD has challenged me in the best ways possible and helped me grow 
          both personally and professionally. 
          This journey at UCSD has laid a strong foundation for my future, and 
          Im excited about the path ahead as I blend my love for mathematics and computer science.
        </p>
      </section>
      <section id="future-aspirations">
        <h2>Future Aspirations</h2>
        <p>
          Im passionate about website development and backend management, and Im excited about pushing 
          the boundaries of what software can do. My goal is to innovate with new methodologies that have 
          real-world impact, optimize existing systems, and dive into advanced research in AI-driven software. 
          Im dedicated to shaping the future of web apps and ensuring that AI development is done responsibly, 
          with a strong emphasis on safety, privacy, and ethics. 
          Ultimately, I want to make a meaningful impact on society, inspire future computer scientists, and work 
          on collaborative projects that blend mathematics with cutting-edge technologies. Im eager to contribute 
          to an exciting future where technology and innovation go hand in hand.
        </p>
      </section>
    </div>
  );
};

export default AboutMe;